.form {
    display: flex;
    justify-content: center;
  }
  
  .form button {
    font: inherit;
    cursor: pointer;
    background-color: #8a2b06;
    border: 1px solid #8a2b06;
    color: white;
    padding: 0.25rem 2rem;
    border-radius: 20px;
    font-weight: bold;
    margin: 5px 15px ;
  }
  
  .form button:hover,
  .form button:active {
    background-color: #641e03;
    border-color: #641e03;
  }

    
  .button {
    animation: bump 300ms ease-out;
    margin-left:auto ;
  }
  
  @keyframes bump {
    
    0% {
      transform: scale(1);
    }
    10% {
      transform: scale(0.9);
    }
    30% {
      transform: scale(1.1);
    }
    50% {
      transform: scale(1.15);
    }
    100% {
      transform: scale(1);
    }
  }

  @media(max-width:1050px){
    .form {
      text-align: left;
      margin: 30px;
      /* display: flex; */
    }
    .form button{
      margin-top:0;
      /* padding: 0 7px 0 7px */
    };
  }
  @media(max-width:350px){
    .form {
      text-align: left;
      margin: 30px;
      /* display: flex; */
    }
    .form button{
      margin: auto;
    };
  }